.tags-view {
    display: flex;
    flex-direction: column;
    gap: 1rem;

    &__characters {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        justify-content: center;
        gap: 0.5rem;
        width: 100%;
        height: 15rem;

        .character-badge { 
            &.not-selected {
                color: white!important;
                background-color: black;
            }
        }

    }

    &__others {
        width: 100%;
        height: 25rem;
        gap: 0.5rem;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;

        .move-type-badge { 
            &.not-selected {
                color: white!important;
                // background-color: black;
                background: black;
            }
        }
    }
}